 import { Link } from "react-router-dom";

// style
import "./header.scss";
// images
import header_logo from "../../asset/img/icon/Group 1.svg";
import header_location from "../../asset/img/header/location.svg";
// import header_search from "../../asset/img/header/search.svg";

export default function Header(){
    return(
        <header className="header"> 
            <section className="header__top">
                <section className="header__top__container _container">
                    <div className="header__top__logo"><Link to={'/'}><img src={header_logo} alt="logo" /></Link></div>
                    <div className="header__top__city"><img src={header_location} alt="city" />Димитровград</div>
                    {/* <div className="header__top__search"><label htmlFor=""><input name="s" placeholder="Искать здесь..." type="search"/><button type="submit"><img src={header_search} alt="" /></button></label></div> */}
                    <div className="header__top__rate">тут будет курс денег</div>
                </section>
            </section>
            <section className="header_center _container">
                <nav>
                    <ul>
                        <li><Link to={'kultura'}>культура</Link></li>
                        <li><Link to={'kino'}>кино</Link></li>
                        <li><Link to={'razvlechenie'}>развлечение</Link></li>
                        <li><Link to={'education'}>обучение</Link></li>
                        {/* <li><Link to={'kino'}>экскурсии</Link></li> */}
                        <li><Link to={'vzroslie'}>для взрослых</Link></li>
                    </ul>
                </nav>
            </section>
            <section className="header_bottom">
                <div className="header_bottom_container _container">
                    <nav>
                        <ul>
                            <li ><Link to={'food'}>еда</Link></li>
                            <li ><Link to={'children'}>дети</Link></li>
                            <li ><Link to={'nature'}>за городом</Link></li>
                            <li ><Link to={'news'}>новости</Link></li>
                            <li><Link to={'zdorovie'}>здоровье</Link></li>
                        </ul>
                    </nav>
                </div>
            </section>
        </header>
    )
}
